@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,600;0,700;1,800&family=Staatliches&display=swap";
* {
    margin: 0;
    padding: 0;
    font-family: Montserrat, sans-serif;
    list-style: none
}

hr {
    width: 70%;
    position: relative;
    left: 15%
}

.navbar {
    color: #fff;
    border-bottom: 1px solid #f5f5f5;
    height: 80px
}

.navbar-brand img {
    height: 40px
}

.navbar-light .navbar-toggler {
    color: #0000;
    border: none
}

.nav-link {
    font-size: 14px;
    font-weight: 700
}

.btn {
    color: #fff;
    text-align: center;
    background-color: #037dd6;
    border-radius: 5px
}

.btn:hover {
    color: #fff;
    background-color: #036bb4
}

.i-btn {
    justify-content: center;
    align-items: center;
    width: 200px;
    padding: 15px;
    display: flex
}

.intro {
    margin-top: -10px;
    padding-top: 100px
}

.intro-content {
    height: 500px
}

.intro-text h2 {
    color: #000;
    margin-top: 50px;
    font-size: 40px
}

.intro-text i {
    color: #fafafa
}

.intro-btn {
    width: 300px;
    height: auto
}

.intro-btn img {
    height: 50px;
    margin: 10px 0
}

.intro-img img {
    width: 90%;
    height: 90%
}

.i-img {
    padding-bottom: 150px
}

.i-img img {
    object-fit: cover;
    width: 100%;
    height: 100%
}

#attribute .a-content {
    height: 100px;
    padding-top: 30px
}

#attribute .a-content .a-card {
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    display: flex
}

#attribute .a-content .a-card i {
    color: #037dd6;
    margin-top: -7px;
    margin-right: 10px;
    font-size: 20px
}

#attribute .a-content .a-card h5 {
    font-weight: 600
}

.cryptos {
    margin-top: 100px
}

.cryptos img {
    height: 180px;
    margin: 50px
}

.r-section {
    background-color: #24292e;
    height: 300px;
    margin-top: 100px
}

.r-section h5 {
    color: #fff
}

#reason .r-content {
    text-align: center;
    height: auto
}

#reason .r-content .lists {
    height: auto;
    padding-bottom: 20px
}

#reason .r-content .lists .lists-card {
    height: auto;
    margin-top: 30px
}

#reason .r-content .lists .lists-card ul li {
    margin: 10px
}

#reason .r-content .lists .lists-card ul li i {
    color: #202020;
    margin-right: 7px
}

#reason .r-content a {
    color: #fff;
    background: #037dd6;
    margin: 30px;
    padding: 10px
}

.main {
    margin-top: 150px
}

.main-content {
    border-radius: 10px;
    height: auto;
    margin-top: 50px;
    box-shadow: 0 0 10px 3px #eeeded
}

footer {
    border-top: 2px solid #ebeaea;
    height: auto;
    margin-top: 50px
}

footer h5 {
    color: #f6851b;
    font-size: 18px
}

footer img {
    height: 100px
}

footer .f-content {
    height: auto
}

footer .f-content ul li {
    margin-bottom: 20px
}

footer .f-content a {
    color: #000000bd;
    font-size: 12px
}

footer .f-content i {
    margin: 0 10px;
    font-size: 20px
}

.wallet {
    text-align: center;
    flex-direction: column;
    margin-top: 40px;
    display: flex
}

.wallet img {
    height: 45px;
    margin: 10px 0
}

.wallet h2 {
    color: #434447;
    margin-top: 30px;
    font-weight: 500
}

.wallet .r-container {
    background: #fff;
    border-radius: 10px;
    display: none;
    box-shadow: 0 0 5px 5px #f0efef
}

#btn1,
#btn2 {
    color: #037dd6;
    cursor: pointer;
    background-color: #0000;
    border: 2px solid #037dd6;
    border-radius: 10px;
    outline: none;
    width: 180px;
    height: 40px;
    margin: 40px auto;
    font-weight: 700
}

#btn1:focus,
#btn2:focus {
    color: #fff;
    background-color: #037dd6
}

.r-container {
    width: 60%;
    margin: 20px auto
}

form {
    padding-top: 30px
}

.form-floating {
    width: 360px;
    margin: 50px auto
}

.restore {
    width: 250px;
    margin-bottom: 40px
}

@media screen and (width<=900px) {
    .navbar {
        padding-right: 10px
    }
    .navbar-brand img {
        height: 40px;
        margin-left: 5px
    }
    .navbar-nav {
        color: #000;
        background-color: #fff
    }
    .navbar-nav .nav-link {
        color: #000
    }
    .intro {
        height: 1000px;
        padding-bottom: 10px
    }
    .i {
        height: 850px
    }
    .intro-text {
        text-align: center;
        margin-bottom: 50px
    }
    .i-btn {
        margin: 10px auto
    }
    .intro-btn {
        margin: 5px auto
    }
    #attribute .a-content {
        height: auto
    }
    #attribute .a-content .a-card {
        margin-top: 20px
    }
    #attribute .a-content .a-card i,
    #attribute .a-content .a-card h5 {
        font-size: 17px
    }
    .cryptos img {
        height: 170px
    }
    .cryptos h4,
    .main h4 {
        font-size: 20px
    }
    .r-container {
        width: 85%
    }
    .wallet h1 {
        font-size: 25px
    }
    .input span {
        margin: 20px 0
    }
}

@media screen and (width<=450px) {
    .cryptos img {
        height: 150px;
        margin: 20px 5px
    }
    .form-floating {
        width: 330px
    }
}

